































import {Vue, Component} from 'vue-property-decorator';
import axios from 'axios';

@Component
export default class Category extends Vue {

    categories: any[] = [];

    // axios
    async created() {
        this.getCategories();
    }

    async getCategories() {
        /**
         * 发送请求到后端，把获取到的真实的分类数据赋值给组件的 categories
         */
        let res = await axios({
            method: 'get',
            url: '/dev/api/admin/category'
        });
        // console.log(res);
        if (res.data.code === 0) {
            this.categories = res.data.data;
        }
    }

    /**
     * 添加
     */
    async add(pid: number) {

        try {
            // 通过 $prompt 来显示一个对话框

            let {value} = await this.$prompt('请输入分类的名称', '添加分类', {
                inputPattern: /\S+/,
                inputErrorMessage: '分类名称不能为空'
            });

            // alert(value);

            let {data} = await axios({
                method: 'post',
                url: '/dev/api/admin/category/add',
                data: {
                    name: value,
                    pid
                }
            });
            if (data.code) {
                // 有错误的
                this.$notify.error( data.message );
            } else {
                // 添加成功的
                this.$notify.success('添加成功');

                // 跟新页面中的分类数据
                this.getCategories();
            } 
        } catch(e){}
    }

    /**
     * 修改分类
     */
    async edit(category: any) {
        try {

            let {value} = await this.$prompt('请输入新的分类名称', '修改分类', {
                inputValue: category.name,
                inputPattern: /[\S+]+/,
                inputErrorMessage: '分类名称不能为空'
            });

            if (value === category.name) {
                return;
            }

            let {data} = await axios({
                method: 'post',
                url: '/dev/api/admin/category/edit',
                data: {
                    id: category.id,
                    name: value
                }
            });

            if (data.code) {
                // 有错误的
                this.$notify.error( data.message );
            } else {
                // 添加成功的
                this.$notify.success('修改成功');

                // 跟新页面中的分类数据
                this.getCategories();
            } 

        } catch(e) {}
    }

    /**
     * 删除
     */
    async remove(id: number) {
        try {
            await this.$confirm('你确定要删除吗？', '删除分类');
            // console.log('删除');

            let {data} = await axios({
                method: 'post',
                url: '/dev/api/admin/category/remove',
                data: {
                    id
                }
            });

            if (data.code) {
            // 有错误的
                this.$notify.error( data.message );
            } else {
                // 添加成功的
                this.$notify.success('修改成功');

                // 跟新页面中的分类数据
                this.getCategories();
            } 
        } catch(e) {
            // $confirm 取消走了catch
            console.log('取消');
        }

        
    }

}

