let hostUrl = '';
if (process.env.NODE_ENV == 'development') {
    hostUrl = '/dev/';
} else {
    hostUrl = '/';
}

export default {
    user: {
        list: hostUrl + 'api/admin/user',
        status: hostUrl + 'api/admin/user/status',
        profile: hostUrl + 'api/admin/user/profile'
    }
}