import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    menus: [
      {
        "text": "首页",
        "name": "/",
        "icon": "iconfont icon-home"
      },
      {
        "text": "精选推荐",
        "name": "/recommend",
        "icon": "iconfont icon-fire"
      },
      {
        "text": "分类",
        "name": "/category",
        "icon": "iconfont icon-category"
      },
      {
        "text": "美食",
        "name": "/food",
        "icon": "iconfont icon-hotfood"
      },
      {
        "text": "菜单",
        "name": "/catemenu",
        "icon": "iconfont icon-navicon-xtcp"
      },
      {
        "text": "用户",
        "name": "/user",
        "icon": "iconfont icon-user"
      },
      {
        "text": "评论",
        "name": "/comment",
        "icon": "iconfont icon-pinglun"
      },
      {
        "text": "收藏",
        "name": "/favorite",
        "icon": "iconfont icon-aixinsoucangshangpin"
      }
    ],
    router: {}
  },
  mutations: {
    setRouterName(state, payload) {
      state.router = payload;

      // 根据传过来的payload，找到menus中对应的对象，赋值给state.router
      let router = state.menus.find( menu => menu.name == payload );
      if (router) {
        state.router = router;
      } else {
        state.router = state.menus[0];
      }
      
    }
  },
  actions: {

  }
})
